.nf-next-previous,
.nf-breadcrumbs {
  content: "";
  list-style-type: none !important;
  margin: 20px 0 !important;
  padding: 0 !important;
}
.nf-next-previous::after,
.nf-breadcrumbs::after {
  clear: both;
  content: "";
  display: block;
}
.nf-next-previous li,
.nf-breadcrumbs li {
  content: "";
  list-style-type: none !important;
  margin: 0 !important;
  padding: 0 !important;
}

.nf-next-previous li.nf-previous-item {
  float: left;
}
.nf-next-previous li.nf-next-item {
  float: right;
}

.nf-breadcrumbs li {
  float: left;
}
.nf-breadcrumbs li a {
  display: inline-block;
  font-size: 16px;
  margin-right: 16px;
  margin-bottom: 10px;
  text-decoration: none;
}
.nf-breadcrumbs li a .nf-breadcrumb-error {
  background: red;
  border-radius: 4px;
  color: #fff;
  padding: 2px 5px;
}
.nf-breadcrumbs li:last-child a {
  margin-right: 0;
}
.nf-breadcrumbs li.active a {
  font-weight: bold;
}

.nf-progress-container {
  margin-top: 20px;
  width: 100%;
  border: 1px solid rgb(187, 187, 187);
}

.nf-progress {
  height: 20px;
  background-color: #329ad1;
}

.nf-next-previous li input {
  font-size: 16px;
  padding: 12px 18px;
}

.nf-breadcrumbs {
  margin: 40px 0 !important;
}
.nf-breadcrumbs li a {
  padding: 12px 18px;
}
.nf-breadcrumbs li.active a {
  font-weight: normal;
}
.nf-breadcrumbs li.errors a {
  position: relative;
  padding-left: 36px;
}
.nf-breadcrumbs li.errors a::after {
  font-family: "FontAwesome";
  position: absolute;
  content: "\f06a";
  left: 12px;
}

.nf-progress-container,
.nf-progress {
  border: 0;
  height: 50px;
  width: 100%;
}

/* ---------------------------------
DEFAULT DARK COLORS
----------------------------------*/
.nf-breadcrumbs li a {
  background: #fff;
  color: #222;
  transition: all 0.5s;
}
.nf-breadcrumbs li a:hover {
  background: rgb(59.5, 59.5, 59.5);
  color: #888888;
  transition: all 0.5s;
}
.nf-breadcrumbs li.active a {
  background: rgb(59.5, 59.5, 59.5);
  color: #888888;
  cursor: default;
}
.nf-breadcrumbs li.errors a {
  background: red;
  color: #fff;
  cursor: help;
}

.nf-progress-container {
  background-color: rgb(59.5, 59.5, 59.5);
}

.nf-progress {
  background-color: #222;
}

